import React from 'react';


function CategoryItem(props) {
    let category = props.category; 
    const categoryIcons = {
        "Conferencias": "microphone", 
        "Cursos": "book", 
        "Espectaculos":"eye",
        "Otros": "circle",
        "Exposiciones":  "camera retro",
        "Cine": "film", 
        "Excursiones":  "globe",
        "Deportes":  "volleyball ball",
        "Fiestas":  "magic"
      }; 
  
    let iconCategory = categoryIcons[category]; 
    let classIconCategory =`${iconCategory} icon`; 
    return (
        <div className="meta">
            <i className={classIconCategory}></i>
            <span> Categoria: &nbsp; 
                <a href={category}>{category}</a>
            </span>
        </div>
    )
}


export default CategoryItem;