import React from 'react';


function Maps(props) {

    const URL_MAP = 'https://maps.googleapis.com/maps/api/staticmap'; 
    const KEY = 'AIzaSyBUXqc_X6bylBgbjKCsfLknAT1Lf1I52hg'; 
    const URL_GOOGLE ='https://www.google.es/maps'

    let HEIGHT = 200; 
    let WITDTH = 200; 
    let ZOOM = 14; 
    let COLOR = 'red'
    let lat = props.latitude; 
    let long = props.longitude; 
    let nameLocation = props.name; 
    let srcGoogleMaps =`${URL_GOOGLE}?q=${lat},${long}`
    let srcMap = `${URL_MAP}?center=${lat}7%2c${long}&markers=color:${COLOR}|${lat},${long}&zoom=${ZOOM}&size=${HEIGHT}x${WITDTH}&key=${KEY}`
    
    return (

        <div className="ui card">
            <div className="image">
                <img 
                    src={srcMap}
                    alt = {nameLocation}
                />
            </div>
            <div className="content">
                <a className="header">{props.name}</a>
                <div className="meta">
                <span className="date">{props.district}</span>
                </div>
                <div className="description">
                    {props.street}, {props.postalCode}, {props.city}
                </div>
            </div>
            <div className="extra content">
    
                <a className="ui " href={srcGoogleMaps} target='blank'>
                    <i className="map icon"></i>  Ver en Google 
                </a>
            </div>
        </div>

    )
}


export default Maps;