import React from 'react'
import { Route, Link, BrowserRouter as Router } from 'react-router-dom'

import App from '../pages/AppPage'
import EventDetail from '../pages/EvenDetailPage'
import EventsLocation from '../pages/EventsLocationsPage';
const EventRouters= () => {
    return (
        <Router>
            <div>
                <Route path="/" exact component={App} />
                <Route path="/evento/:title/:id"  component={EventDetail} />
                <Route path="/lugar/:location/:id"  component={EventsLocation} />
               
            </div>
        </Router>
    );
}

export default EventRouters;